.main-body{
  width: 100%;
  background-color: $light_blue_bg;
  &.lightblue{
    background-color: $light_blue_bg;
  }
  &.blue{
    background-color: $blue_bg;
    hr{
      border-color: $blue_bg - #222;
    }
  }
  .middle-container{
    width: $main_width;
    min-width: 300px;
    margin: 0 auto;
    padding: 50px 0;
    &.single-column{
      display: block;
    }
    .left-column{
      vertical-align: top;
    }
    .right-column{
      padding-left:100px;
      p{
        color: darken($botwinder_blue, 18);
        max-width: 1080px;
      }
      &.row {
        .row-child {
          padding-bottom: 50px;
        }
      }
    }
    .float-middle {
      width: 100%;
      h1,h2,h3,h4,h5,h6{
        text-align: center;
      }
      p, div {
        text-align: center;
      }
    }
    .section-big {
      h1 {
        font-size: 36pt;
      }
      p, div {
        font-size: 16pt;
      }
    }
    //@media (max-width: $media_tablet_size) {
    //    width: $main_width_tablet;
    //    padding-left:50px;
    //    padding-right:50px;
    //}
  }

}
.navbar{
    padding: 0 !important;
    border-radius: 0 !important;
}
.navbar-nav .nav-item + .nav-item {
    margin-left: 0 !important;
}
header{
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  > img{
    display: inline-block;
  }
  @media (max-width: 767px) {
    position: fixed;
    z-index: 999;
  }
  nav {
    //width: $main_width;
    font-family: 'Alegreya SC', serif;
    min-width: 848px;
    margin: 0 auto;
    position: relative;
    //@media (max-width: $media-desktop-min) {
    //  width: $main_width_tablet;
    //}
    @media (max-width: 767px) {
      .navbar-toggler[aria-expanded="true"] {
        -webkit-border-radius:0;
        -moz-border-radius:0;
        border-radius:0;
        background-color: $light_blue_bg;
      }
      #collapsingNavbar {
        position: absolute;
        width: 100%;
        z-index: 999;
        top:38px
      }
    }
  }
  ul{
    width: 100%;
    vertical-align: top;
    padding: 0;
    margin: 0;
    //display: inline-flex;
    //list-style: none;
    //align-items: flex-end;
    li{
      //flex-direction: row;
      //flex-wrap: nowrap;
      a{
        @media (max-width: 767px) {
          text-align: left;
          padding: 10px 17px !important;
        }
        @media (min-width:768px) and (max-width: 991px) {
          padding: 5px 13px !important;
          img {
              height: 40%;
              width: auto;
          }
        }
        //height: 95px;
        height: auto;
        text-align: center;
        background-color: #ffffff;
        display: block;
        color: $botwinder_blue !important;
        font-size: 12pt;
        text-decoration: none;
        padding: 10px 20px !important;
        -webkit-transition: $nav_link_hovertime ease-in-out all;-moz-transition: $nav_link_hovertime ease-in-out all;-ms-transition: $nav_link_hovertime ease-in-out all;-o-transition: $nav_link_hovertime ease-in-out all;transition: $nav_link_hovertime ease-in-out all;
        &:visited{
          color: $botwinder_blue !important;
        }
        &:hover{
          color: $botwinder_blue - #111 !important;
          background-color: saturate($light_blue_bg - #101010, 10);
        }
        &.active{
          color: $botwinder_blue - #111 !important;
          background-color: $light_blue_bg !important;
        }
        &:hover{
          -webkit-transition: $nav_link_hovertime ease-in-out all;-moz-transition: $nav_link_hovertime ease-in-out all;-ms-transition: $nav_link_hovertime ease-in-out all;-o-transition: $nav_link_hovertime ease-in-out all;transition: $nav_link_hovertime ease-in-out all;
        }
      }
    }
  }
}
