table.command{
  border: none;
  th, td {
    vertical-align: top;
    padding: 1px 20px;
  }
}

table.commands {
  border-collapse: collapse;
  border: 1px solid $table_border_color;
  th, td {
    padding: 10px 20px;
  }
  thead{
    background-color: $botwinder_blue;
    th {
      color: white;
      letter-spacing: 0.0241em;
      border-top: 1px solid $table_border_color;
      border-right: 1px solid desaturate($table_border_color, 10);
      border-bottom: 1px solid desaturate($table_border_color, 10);
      &:first-child{
        border-left: 1px solid $table_border_color;
      }
      &:last-child{
        border-right: 1px solid $table_border_color;
      }
    }
  }
  tbody{
    background-color: #b9d5ff;
    tr:nth-child(odd) {
      background-color: #a6c9fe;
    }
    td{
      border-right: 1px solid desaturate($table_border_color+#222, 10);
      border-bottom: 1px solid desaturate($table_border_color+#222, 10);
      &:first-child{
        border-left: 1px solid $table_border_color;
      }
      &:last-child{
        border-right: 1px solid $table_border_color;
      }
    }
    tr:last-child td {
      border-bottom: 1px solid $table_border_color;
    }
  }
}
