  background: #e4f8ff;
  border: none;
  border-right: 1px solid #d0e2e9;
  border-top-left-radius: .25rem !important;
  border-bottom-left-radius: .25rem !important;
}

.listItemGroup .input-group-addon:hover {
  background: #d4f1fb;
  border-color: #cad6da;
}

.listItemGroup .input-group-addon:active {
  background: #c1eaf9;
  border-color: #cad6da;
}

.input-group {
  border-spacing: 0;
}

.inputList {
  width: 300px;
}

.inputList .input-group-btn {
  width: 1% !important;
}

.inputList .selectList {
  max-width: 100%;
}

.inputList .listItem {
  font-family: 'Source Code Pro', 'Consolas', sans-serif;
}

.inline {
  display: inline-block;
}

.v-select .dropdown-toggle {
  background-color: white !important;
  border: 1px solid #a7bfd9 !important;
}

.v-select .selected-tag {
  background-color: #e4f8ff !important;
  border: 1px solid #d0e2e9 !important;
}

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 300px;
  margin: 0 auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all .3s ease;
}

.modal-body {
  margin: 20px 0;
}

.modal-body p:first-child {
  margin-top: 0;
}

.modal-body ul {
  padding-left: 0;
}

.modal-default-button {
  float: right;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */
.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
